.App {
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(135deg, #0f2027 0%, #203a43 50%, #2c5364 100%);
}

.developer-banner {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 24px;
  position: relative;
  z-index: 2;
}

.developer-banner.inside {
  justify-content: flex-end;
}

.developer-info {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 12px 20px;
  background: rgba(255, 255, 255, 0.85);
  border-radius: 40px;
  box-shadow: 0 10px 30px rgba(20, 33, 61, 0.15);
  backdrop-filter: blur(8px);
}

.developer-avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid rgba(102, 126, 234, 0.8);
  box-shadow: 0 6px 16px rgba(102, 126, 234, 0.35);
}

.developer-label {
  margin: 0;
  font-size: 0.75rem;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #667eea;
  font-weight: 700;
}

.developer-name {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 700;
  color: #1f2a44;
}

.developer-role {
  margin: 0;
  font-size: 0.9rem;
  color: #5a6a85;
}

.App.guest {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  overflow: hidden;
}

.App.guest::before {
  content: '';
  position: absolute;
  inset: -100px;
  background-image:
    linear-gradient(120deg, rgba(15, 32, 39, 0.7), rgba(44, 83, 100, 0.7)),
    url('https://images.unsplash.com/photo-1522202176988-66273c2fd55f?auto=format&fit=crop&w=1400&q=80'),
    url('https://images.unsplash.com/photo-1522071820081-009f0129c71c?auto=format&fit=crop&w=1600&q=80'),
    url('https://images.unsplash.com/photo-1523240795612-9a05460b166f?auto=format&fit=crop&w=1500&q=80');
  background-position: center, top left, center right, bottom left;
  background-size: cover, 60%, 55%, 60%;
  background-repeat: no-repeat;
  transform: scale(1.05);
  opacity: 0.9;
  z-index: 0;
  pointer-events: none;
}

.App.guest::after {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, rgba(15, 32, 39, 0.3), rgba(12, 46, 60, 0.6));
  z-index: 0;
  pointer-events: none;
}

.login-wrapper {
  display: grid;
  grid-template-columns: minmax(280px, 1fr) 420px;
  max-width: 1080px;
  width: 100%;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 32px;
  overflow: hidden;
  box-shadow: 0 40px 80px rgba(15, 32, 39, 0.35);
  backdrop-filter: blur(10px);
  position: relative;
  z-index: 1;
}

.login-showcase {
  position: relative;
  z-index: 1;
  padding: 60px;
  background: radial-gradient(circle at top left, rgba(102, 126, 234, 0.65), rgba(47, 53, 79, 0.9));
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}

.login-showcase .showcase-badge {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 999px;
  padding: 8px 16px;
  font-size: 0.85rem;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.login-showcase h1 {
  font-size: 2.8rem;
  margin: 0;
  color: #fff;
}

.login-showcase p {
  font-size: 1.05rem;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.85);
}

.login-showcase ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: rgba(255, 255, 255, 0.85);
}

.login-showcase ul li::before {
  content: 'â€¢';
  margin-right: 10px;
  color: rgba(255, 255, 255, 0.7);
}

.login-card {
  padding: 50px 40px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  background: rgba(255, 255, 255, 0.78);
  backdrop-filter: blur(18px);
  border: 1px solid rgba(255, 255, 255, 0.45);
  box-shadow: 0 25px 60px rgba(18, 28, 40, 0.25);
  position: relative;
  z-index: 1;
}

.tabs.wide {
  justify-content: space-between;
}

.auth-form.modern {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.auth-form.modern input {
  width: 100%;
  padding: 14px 16px;
  border-radius: 12px;
  border: 1px solid #dbe2f4;
  background: #f8faff;
  font-size: 0.95rem;
  transition: border 0.2s ease, box-shadow 0.2s ease;
}

.auth-form.modern input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.15);
  background: #fff;
}

.primary-btn {
  padding: 14px 20px;
  border: none;
  border-radius: 12px;
  background: linear-gradient(130deg, #667eea 0%, #764ba2 100%);
  color: white;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 16px 35px rgba(102, 126, 234, 0.35);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.primary-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 20px 40px rgba(102, 126, 234, 0.45);
}

.primary-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  box-shadow: none;
}

.auth-message {
  padding: 12px 14px;
  border-radius: 10px;
  font-size: 0.9rem;
}

.auth-message.error {
  background: #ffe9e9;
  color: #b13434;
}

.auth-message.success {
  background: #e4f7e7;
  color: #1f7a3d;
}

.card-footer {
  font-size: 0.8rem;
  color: #8c8fa5;
  line-height: 1.4;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  background: white;
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 10px;
  font-size: 2.5em;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.brand {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.logo {
  font-weight: 800;
  font-size: 1.4rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.tagline {
  color: #666;
  font-size: 0.85rem;
}

.auth-area .auth-form .row {
  display: flex;
  gap: 10px;
  align-items: center;
}

.auth-area input {
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.tabs {
  display: flex;
  gap: 8px;
  margin-bottom: 10px;
}

.tab {
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 20px;
  background: #f8f9fa;
  cursor: pointer;
}

.tab.active {
  border-color: #667eea;
  color: #667eea;
  background: #eef0ff;
}

.welcome {
  display: flex;
  gap: 12px;
  align-items: center;
}

.logout-btn {
  padding: 8px 12px;
  background: #f44336;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 10px;
  font-size: 2.5em;
}

.subtitle {
  text-align: center;
  color: #666;
  margin-bottom: 40px;
  font-size: 1.1em;
}

.upload-sections {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 30px;
  margin-bottom: 30px;
}

.upload-section {
  background: #f8f9fa;
  border-radius: 15px;
  padding: 25px;
  border: 2px dashed #667eea;
  transition: all 0.3s ease;
}

.upload-section:hover {
  border-color: #764ba2;
  background: #f0f0f0;
}

.upload-section h2 {
  color: #667eea;
  margin-bottom: 20px;
  font-size: 1.5em;
}

.upload-box {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.file-input {
  padding: 12px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: border-color 0.3s ease;
}

.file-input:hover {
  border-color: #667eea;
}

.file-input:focus {
  outline: none;
  border-color: #667eea;
}

.file-info {
  background: white;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #667eea;
}

.file-info p {
  margin: 5px 0;
  color: #555;
}

.file-info ul {
  margin: 10px 0;
  padding-left: 20px;
}

.file-info li {
  margin: 5px 0;
  color: #555;
}

.upload-btn {
  padding: 12px 24px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.upload-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.upload-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.message {
  padding: 15px;
  border-radius: 8px;
  margin: 20px 0;
  font-size: 16px;
}

.message.error {
  background: #fee;
  border-left: 4px solid #f00;
  color: #c00;
}

.message.success {
  background: #efe;
  border-left: 4px solid #0a0;
  color: #060;
}

.uploaded-files {
  margin-top: 40px;
}

.uploaded-files h2 {
  color: #333;
  margin-bottom: 20px;
  font-size: 1.8em;
}

.files-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.file-card {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 20px;
  border: 1px solid #ddd;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.file-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}

.file-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.file-header h3 {
  color: #667eea;
  margin: 0 0 5px 0;
  word-break: break-word;
  font-size: 1.1em;
}

.file-owner {
  margin: 0;
  font-size: 0.85em;
  color: #888;
  font-style: italic;
}

.edit-btn {
  padding: 6px 12px;
  background: #667eea;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 0.85em;
  cursor: pointer;
  transition: background 0.2s ease;
}

.edit-btn:hover {
  background: #5568d3;
}

.file-description {
  background: #f0f4ff;
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 15px;
  border-left: 3px solid #667eea;
}

.file-description p {
  margin: 0;
  color: #555;
  line-height: 1.5;
}

.edit-description {
  margin-bottom: 15px;
}

.edit-actions {
  display: flex;
  gap: 8px;
  margin-top: 8px;
}

.save-btn, .cancel-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  font-size: 0.9em;
  cursor: pointer;
  transition: all 0.2s ease;
}

.save-btn {
  background: #28a745;
  color: white;
}

.save-btn:hover {
  background: #218838;
}

.cancel-btn {
  background: #6c757d;
  color: white;
}

.cancel-btn:hover {
  background: #5a6268;
}

.file-details {
  margin-bottom: 15px;
}

.file-details p {
  margin: 5px 0;
  color: #666;
  font-size: 0.9em;
}

.file-actions {
  display: flex;
  gap: 10px;
}

.social-actions {
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 15px;
}

.like-btn {
  padding: 8px 12px;
  background: #f8f9fa;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 0.9em;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 5px;
}

.like-btn:hover {
  background: #e9ecef;
  border-color: #667eea;
}

.like-btn.liked {
  background: #ffe9e9;
  border-color: #f44336;
  color: #f44336;
}

.description-input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 0.95rem;
  font-family: inherit;
  resize: vertical;
  margin-bottom: 10px;
  transition: border-color 0.2s ease;
}

.description-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.comments-section {
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid #eee;
}

.comments-list {
  margin-bottom: 12px;
  max-height: 200px;
  overflow-y: auto;
}

.comment-item {
  padding: 8px 0;
  font-size: 0.9em;
  color: #555;
  border-bottom: 1px solid #f0f0f0;
}

.comment-item:last-child {
  border-bottom: none;
}

.comment-item strong {
  color: #667eea;
  margin-right: 5px;
}

.comment-input {
  display: flex;
  gap: 8px;
}

.comment-input input {
  flex: 1;
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 0.9em;
}

.comment-input input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 2px rgba(102, 126, 234, 0.1);
}

.comment-btn {
  padding: 8px 16px;
  background: #667eea;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 0.9em;
  cursor: pointer;
  transition: background 0.2s ease;
}

.comment-btn:hover {
  background: #5568d3;
}

.view-btn,
.copy-btn {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.view-btn {
  background: #667eea;
  color: white;
  text-decoration: none;
  text-align: center;
  display: inline-block;
}

.view-btn:hover {
  background: #5568d3;
}

.copy-btn {
  background: #28a745;
  color: white;
}

.copy-btn:hover {
  background: #218838;
}

@media (max-width: 768px) {
  .App.guest {
    padding: 40px 16px;
  }

  .developer-banner,
  .developer-banner.inside {
    justify-content: center;
  }

  .developer-info {
    flex-direction: column;
    text-align: center;
    border-radius: 24px;
  }

  .login-wrapper {
    grid-template-columns: 1fr;
    border-radius: 24px;
  }

  .login-showcase {
    padding: 40px 28px;
  }

  .login-card {
    padding: 32px 24px 40px;
  }

  .upload-sections {
    grid-template-columns: 1fr;
  }

  .container {
    padding: 20px;
  }

  h1 {
    font-size: 2em;
  }
}

